*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font: normal 16px/1.8 'Open Sans', arial, sans-serif;
  color: #000;
}
h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  line-height: 1.1;
  font-family: 'Roboto Condensed', arial, sans-serif;
  font-weight: 700;
  color: #00aeef;
}
h1,
.h1 {
  margin: 0 0 30px 0;
}
h2,
h3,
.h2,
.h3 {
  margin: 20px 0;
}
h4,
h5,
h6,
.h4,
.h5,
.h6 {
  margin: 10px 0;
}
h1,
.h1 {
  font-size: 42px;
}
h2,
.h2 {
  font-size: 35px;
}
h3,
.h3 {
  font-size: 30px;
}
h4,
.h4 {
  font-size: 25px;
}
h5,
.h5 {
  font-size: 20px;
}
h6,
.h6 {
  font-size: 18px;
}
a {
  color: #00aeef;
  text-decoration: none;
  transition: all 250ms ease;
}
a:hover {
  text-decoration: underline;
}
a img {
  border: 0;
}
img {
  max-width: 100%;
  height: auto !important;
}
p {
  margin: 20px 0;
}
ol,
ul {
  margin: 25px;
}
ol li,
ul li {
  margin: 10px 0;
}
ol li > ul,
ul li > ul,
ol li > ol,
ul li > ol {
  margin: 5px 0 10px 25px;
}
dd {
  margin: 20px;
}
hr {
  border: 0;
  border-bottom: 1px solid #f3f3f3;
  height: 0px;
  margin: 30px 0;
  padding: 0;
  width: 100%;
}
pre,
code,
blockquote {
  padding: 30px;
  background: #f3f3f3;
}
*:first-child {
  margin-top: 0;
}
*:last-child {
  margin-bottom: 0;
}
.mceTemp + * {
  margin-top: 0;
}
.alignleft {
  display: block;
  float: left;
  margin-right: 30px;
}
.alignright {
  display: block;
  float: right;
  margin-left: 30px;
}
.aligncenter {
  display: block;
  margin: 20px auto;
  clear: both;
}
.wp-caption-dd {
  padding: 0;
  margin: 5px 0 0;
  text-align: center;
  font-size: 12px;
}
